* {
    box-sizing: border-box;
  }
  
  body {
    display: flex;
    font-family: sans-serif;
    /* background: #18f235;
      background: linear-gradient(to right, #f64f59, #c471ed, #12c2e9); */
  }
  
  .body_Profilepage {
    display: flex;
    font-family: sans-serif;
    /* background: #18f235;
      background: linear-gradient(to right, #f64f59, #c471ed, #12c2e9); */
  }
  
  body,
  html,
  .App,
  #root,
  .outer {
    width: 100%;
    height: 100%;
  }
  
  .header {
    text-align: center;
    margin: 0;
    padding-bottom: 30px;
  }
  
  .forms {
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: left;
    padding-bottom: 10px;
  }
  
  .form-control {
    padding: 10px 10px;
    margin: 3px 0;
    border: 1px solid grey;
    border-radius: 8px;
  }
  
  .outer {
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: left;
  }
  
  .inner {
    width: 50%;
    margin: auto;
    background: #ffffff;
    box-shadow: 0px 10px 80px rgba(34, 35, 58, 0.5);
    padding: 40px 55px 45px 55px;
    border-radius: 15px;
    transition: all 0.3s;
  }
  
  .submit-button {
    background-color: #f64f59;
    transition-duration: 1s;
    border: none;
    border-radius: 10px;
    color: white;
    padding: 15px 32px;
    text-align: center;
    font-size: 16px;
  }
  
  .submit-button:hover {
    background-color: #12c2e9;
    color: white;
  }
  